@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body{
  background: #272727;
  overflow: hidden;
}
@media (max-width: 400px){
    .player.full .container{
      width: 100% !important;
    }
    .player.full .container .info .data{
      margin-left: 10px;
    }
    .player.full .container .info .data h2{
      font-size: 0.8em;
    }
    .player.full .container .info .data p{
      font-size: 0.8em;
      line-height: 10px !important;
    }
    .player.full .container .info{
      width: 70% !important;
    }
}

.ScreenContent{width:100vw;height:100vh;z-index:0;background:#fff;background-image:url(/static/media/bg.7d4c90f6.png);background-repeat:no-repeat}@-webkit-keyframes bola_rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes bola_rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@-webkit-keyframes bola_desplazar{from{right:-100px}to{right:100%}}@keyframes bola_desplazar{from{right:-100px}to{right:100%}}.backdrop{background:rgba(0,0,0,0.4);width:100vw;height:100%;position:absolute;top:0}.backdrop .centered{display:flex;width:100%;height:auto;z-index:0;justify-content:center;margin-top:20vh}.backdrop .centered .logo{background-image:url(/static/media/logo.737e73f0.png);background-repeat:no-repeat;width:300px;height:300px;background-size:100%}.backdrop .buttons{width:100vw;height:calc(100vw / 5);display:flex;position:absolute;bottom:0;justify-content:center}.backdrop .buttons button{width:calc(100vw / 10);height:calc(100vw / 10);background:#fff;min-height:100px;min-width:100px;border:none;outline:none;cursor:pointer}.shaked{-webkit-animation:shake 1s cubic-bezier(0, 0, 0, 0) both infinite alternate;animation:shake 1s cubic-bezier(0, 0, 0, 0) both infinite alternate}@-webkit-keyframes shake{10%,90%{transform:translate3d(-1px, 0, 0)}20%,80%{transform:translate3d(2px, -3px, 0)}30%,50%,70%{transform:translate3d(-4px, 0, 0)}40%,60%{transform:translate3d(4px, -9px, 0)}}@keyframes shake{10%,90%{transform:translate3d(-1px, 0, 0)}20%,80%{transform:translate3d(2px, -3px, 0)}30%,50%,70%{transform:translate3d(-4px, 0, 0)}40%,60%{transform:translate3d(4px, -9px, 0)}}

.player.full{width:100vw;height:15vh;max-height:120px;background:#000;position:absolute;bottom:0}.player.full .container{width:80%;height:100%;margin:auto;display:flex}.player.full .container .info{width:30%;height:100%;background:auto;display:flex;align-items:center;justify-content:space-around}.player.full .container .info img{height:80%;width:auto}.player.full .container .info .data{height:calc(100%);width:70%;background:#000}.player.full .container .info .data h2,.player.full .container .info .data p{color:#f5f5f5;line-height:25px;-webkit-margin-after:0 !important;margin-block-end:0 !important}.player.full .container .info .data h2{-webkit-margin-after:0 !important;margin-block-end:0 !important}.playerbutton{stroke:#f5f5f5;width:60px;stroke-width:4;margin:auto;cursor:pointer}.slider{-webkit-appearance:none;width:100%;height:15px;border:4px solid #fff;border-radius:0px;background:transparent;outline:none;opacity:0.7;transition:opacity .2s}.slider:hover{opacity:1}.slider::-webkit-slider-thumb{-webkit-appearance:none;appearance:none;width:30px;height:30px;background:#f5f5f5;border:3px solid #f5f5f5;cursor:pointer}.slider::-moz-range-thumb{width:30px;height:30px;background:transparent;border:3px solid #f5f5f5;cursor:pointer}

.history{height:auto;min-height:200px;width:100vw;background:#000;position:absolute;bottom:-207px;z-index:-1;border-top:5px dashed #eeeeee}.history .item{height:100px;width:100vw;border-bottom:1px dashed #fff}.history .item .content{width:80%;height:100%;margin:auto}

.PreloadScreen{width:100vw;height:100vh;z-index:1;position:absolute;top:0;left:0;display:flex;justify-content:center;align-items:center;background:#272727}.PreloadScreen .logo{background-image:url(/static/media/logo.802c41a9.webp);width:300px;height:300px;background-size:100%;-webkit-animation:pulse .7s infinite alternate;animation:pulse .7s infinite alternate}.PreloadScreen .none{display:none}.out{-webkit-animation:out 1s forwards;animation:out 1s forwards;-webkit-animation-iteration-count:1;animation-iteration-count:1}@-webkit-keyframes out{0%{-webkit-clip-path:polygon(0 0, 100% 0, 100% 100%, 0% 100%);clip-path:polygon(0 0, 100% 0, 100% 100%, 0% 100%)}100%{-webkit-clip-path:polygon(0 0, 0 0, 0 100%, 0% 100%);clip-path:polygon(0 0, 0 0, 0 100%, 0% 100%)}}@keyframes out{0%{-webkit-clip-path:polygon(0 0, 100% 0, 100% 100%, 0% 100%);clip-path:polygon(0 0, 100% 0, 100% 100%, 0% 100%)}100%{-webkit-clip-path:polygon(0 0, 0 0, 0 100%, 0% 100%);clip-path:polygon(0 0, 0 0, 0 100%, 0% 100%)}}@-webkit-keyframes pulse{0%{transform:scale(1)}100%{transform:scale(1.3)}}@keyframes pulse{0%{transform:scale(1)}100%{transform:scale(1.3)}}

