.PreloadScreen{
  width: 100vw;
  height: 100vh;
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #272727;

  .logo{
    background-image: url("../../assets/logo.webp");
    width: 300px;
    height: 300px;
    background-size: 100%;
    animation: pulse .7s infinite alternate;
  }
  .none{
    display: none;
  }
}
.out{
  animation: out 1s forwards;
  animation-iteration-count: 1;

}
@keyframes out{
  0%{clip-path: polygon(0 0, 100% 0, 100% 100%, 0% 100%);}
  100%{clip-path: polygon(0 0, 0 0, 0 100%, 0% 100%);}
}
@keyframes pulse{
  0%{transform: scale(1);}
  100%{transform: scale(1.3)}
}
