body{
  background: #272727;
  overflow: hidden;
}
@media (max-width: 400px){
    .player.full .container{
      width: 100% !important;
    }
    .player.full .container .info .data{
      margin-left: 10px;
    }
    .player.full .container .info .data h2{
      font-size: 0.8em;
    }
    .player.full .container .info .data p{
      font-size: 0.8em;
      line-height: 10px !important;
    }
    .player.full .container .info{
      width: 70% !important;
    }
}
